import './floor-plans.scss';
import FloorPlan from '../services/floor-plan.service';
import floorPlanDirective from '../directives/floor-plan.directive';
import floorPlanSettingsDirective from '../directives/floor-plan-settings.directive';
import floorPlanEmbeddingDirective from '../directives/floor-plan-embedding.directive';
import ReservationUnit from 'shared/services/inventory/reservation-unit.service';

export default angular.module('deskworks.FloorPlansCtrl', [
  'autoSave',
  'deskworks.reservationCategory.service',
  FloorPlan.name,
  floorPlanDirective.name,
  floorPlanSettingsDirective.name,
  floorPlanEmbeddingDirective.name,
  ReservationUnit.name,
  'deskworks.OurMembers',
])

  .controller('FloorPlansCtrl', function($scope, $translate, $filter, $q, $window, $timeout, session, dwAlerts, state, helper, autoSave,
                                         FloorPlan, ReservationUnit, reservationCategoryService, OurMembers) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('FLOOR_PLANS.PAGE_TITLE'));

    /**
     * On Init
     */
    $ctrl.onInit = () => {
      $timeout(() => {
        $ctrl.view = 'community';

        $ctrl.fetch()
          .then(results => {
            const { plans, reservationCategories, reservationUnits, members } = results;

            $ctrl.categories = reservationCategories.map(cat => helper.pick(cat, ['id', 'name']));

            $ctrl.units = reservationUnits.map(unit => {
              const result = helper.pick(unit, ['id', 'name', 'description', 'area', 'capacity', 'thumbnail', 'photo',
                'attachedUserId', 'attachedUserName', 'availableFullTime',
                'fullTimeProductId', 'fullTimeProductName', 'fullTimePrice', 'fullTimeNet', 'fullTimeDiscount',
                'reservationTypeId']);
              if (result.attachedUserId) result.$$user = members.find(m => m.id === result.attachedUserId);
              return result;
            });

            $ctrl.plan = $ctrl.plans[0];
            $ctrl.plans.forEach(plan => {
              plan.points.forEach(point => {
                point.$$unit = $ctrl.units.find(u => u.id === point.reservationUnitId);
                point.$$category = point.$$unit && $ctrl.categories.find(c => c.id === point.$$unit.reservationTypeId);
              });
            });

            /**
             * Pre-calculate categories and units usage
             */
            $scope.$watchCollection(() => $ctrl.plan ? $ctrl.plan.points.map(p => (p.$$unit || {}).id) : [], () => {
              $ctrl.units.forEach(unit => { unit.$$hasPoint = !!$ctrl.plan.points.find(p => p.$$unit === unit); });
              $ctrl.categories.forEach(cat => { cat.$$hasUnits = !!$ctrl.units.find(u => !u.$$hasPoint && u.reservationTypeId === cat.id); });
            });
          });

        /**
         * Reset selected point on switching plan
         */
        $scope.$watch('$ctrl.plan', () => { $ctrl.point = null });
      });
    };

    $ctrl.isAvailable = unit => {
      return !!unit && unit.availableFullTime;
    };

    $ctrl.fetchPlans = () => FloorPlan.query(state.getCurrentCenterId())
      .catch(err => dwAlerts.error(err, 'Failed to query floor plans.'))
      .then(plans => {
        $ctrl.plans = plans;
      });

    /**
     * Fetch data
     */
    $ctrl.fetch = () => {
      const promises = {};

      promises.plans = $ctrl.fetchPlans();

      promises.reservationCategories = reservationCategoryService.query(state.getCurrentCenterId())
        .catch(err => dwAlerts.error(err, 'Failed to query reservation categories.'));

      promises.reservationUnits = ReservationUnit.query(state.getCurrentCenterId())
        .catch(err => dwAlerts.error(err, 'Failed to query reservation units.'));

      promises.members = OurMembers.query({ centerId: state.getCurrentCenterId(), limit: 10000 })
        .catch(err => dwAlerts.error(err, 'Failed to query our members.'));

      return $q.all(promises);
    };

    /**
     * Save plan
     */
    $ctrl.save = autoSave.wrap(save);
    function save() {
      if (helper.pointFirstInvalid('floor-plan-settings')) return;

      const plan = helper.pick($ctrl.plan, ['id', 'name', 'enabled', 'pricingEnabled', 'areaEnabled', 'points']);
      plan.points = plan.points.filter(p => !!(p.$$unit && (p.reservationUnitId = p.$$unit.id)));

      return FloorPlan.save(state.getCurrentCenterId(), plan)
        .catch(err => dwAlerts.error(err, 'Failed to save floor plan'))
        .then(result => { $ctrl.plan.id = result.id });
    }

    /**
     * Delete plan
     */
    $ctrl.deletePlan = (plan) => {
      if (plan.id && !$window.confirm($translate.instant('FLOOR_PLANS.CONFIRM_DELETE'))) return;

      return (plan.id ? FloorPlan.delete(plan.id) : $q.when())
        .catch(err => dwAlerts.error(err, 'Failed to delete floor plan.'))
        .then(() => {
          $ctrl.plans.splice($ctrl.plans.indexOf(plan), 1);
          if (plan === $ctrl.plan) $ctrl.plan = $ctrl.plans[0];
        });
    };

    /**
     * Upload plan image
     */
    $ctrl.uploadPlanImage = (plan, $files, $invalidFiles) => {
      if (helper.pointFirstInvalid('floor-plan-settings')) return;

      if ($invalidFiles && $invalidFiles.length > 0) return dwAlerts.error($translate.instant('FLOOR_PLANS.IMAGE.INVALID_FORMAT'));
      if (!plan || !plan.id || !$files || !$files.length) return;

      plan.$$spinUpload = true;
      return FloorPlan.uploadImage(plan.id, $files[0])
        .catch(err => dwAlerts.error(err, 'Failed to upload floor plan image.'))
        .finally(() => { plan.$$spinUpload = false; })
        .then(result => { plan.thumbnail = result.data.thumbnail + '?' + Date.now(); plan.image = result.data.image + '?' + Date.now(); });
    };

    $ctrl.onInit();
  });
