import './floor-plan.scss';
import template from  './floor-plan.tpl.html';
import floorPlanPointDetailsDirective from "./floor-plan-point-details.directive";
import floorPlanPointSettingsDirective from "./floor-plan-point-settings.directive";
import dnd from 'shared/drag-n-drop/dnd.service';

export default angular.module('deskworks.floorPlanDirective', [
  dnd.name,
  floorPlanPointDetailsDirective.name,
  floorPlanPointSettingsDirective.name,
])

  .directive('floorPlan', function() {
    return {
      template,
      restrict: 'E',
      scope: true,
      controller: 'FloorPlanDirectiveCtrl as $ctrl',
      transclude: {
        hoverTip: '?hoverTip',
        focusContent: '?focusContent',
        noFocusContent: '?noFocusContent',
      },
      bindToController: {
        plan: '<',
        point: '=?',
        save: '&',
        isAvailable: '<?',
      },
    };
  })

  .controller('FloorPlanDirectiveCtrl', function($element, $scope, $window, state, dwAlerts, helper) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
    };

    /**
     * Click point
     */
    $ctrl.onPointClick = (point, event) => {
      $ctrl.point = point;
      if (event) event.stopPropagation();
    };

    /**
     * Click floor plan
     */
    $ctrl.onPlanClick = (event) => {
      if (!$scope.ffBlock.editMode) { return; }
      const img = $element[0].querySelector('.fp-image');
      const point = {
        x: helper.round(event.offsetX * 100 / img.offsetWidth),
        y: helper.round(event.offsetY * 100 / img.offsetHeight),
      };
      $ctrl.plan.points.push(point);
      $ctrl.onPointClick(point);
    };

    /**
     * Drop point after dragging
     */
    $ctrl.dropPoint = (list, index, event) => {
      if (!$scope.ffBlock.editMode) { return; }
      const point = list[index];

      const img = $element[0].querySelector('.fp-image');
      point.x = helper.round(event.offsetX * 100 / img.offsetWidth);
      point.y = helper.round(event.offsetY * 100 / img.offsetHeight);

      $ctrl.save($ctrl.plan);

      return true;
    };
  });
