import './floor-plans.scss';
import template from  './floor-plans.tpl.html';

export default angular.module('deskworks.floorPlansDirective', [
])

  .directive('floorPlans', function() {
    return {
      template,
      restrict: 'E',
      scope: true,
      controller: 'FloorPlansDirectiveCtrl as $ctrl',
      transclude: {
        hoverTip: '?hoverTip',
        focusContent: '?focusContent',
        noFocusContent: '?noFocusContent',
        selectorExtra: '?selectorExtra',
        planSettings: '?planSettings',
        bottomExtra: '?bottomExtra',
      },
      bindToController: {
        plans: '<',
        plan: '=?',
        point: '=?',
        save: '&',
        isAvailable: '<?',
      },
    };
  })

  .controller('FloorPlansDirectiveCtrl', function($scope, FloorPlan) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
    };

    $ctrl.$onChanges = () => {
      if ($ctrl.plans && !$ctrl.plan) {
        $ctrl.plan = $ctrl.plans[0];
      }
    };

    /**
     * New plan
     */
    $ctrl.addPlan = () => {
      $ctrl.plan = FloorPlan.new();
      $ctrl.plans.push($ctrl.plan);
    };
  });
