import FloorPlan from '../services/floor-plan.service';
import floorPlansDirective from '../directives/floor-plans.directive';
import ReservationUnit from 'shared/services/inventory/reservation-unit.service';
import resBar from 'shared/res-bar/res-bar.component';

export default angular.module('deskworks.FloorPlansReservationCtrl', [
  'deskworks.reservationCategory.service',
  'deskworks.calendarService',
  FloorPlan.name,
  floorPlansDirective.name,
  ReservationUnit.name,
  resBar.name,
])

  .controller('FloorPlansReservationCtrl', function($scope, $translate, $filter, $q, $window, $timeout,
                                                    session, dwAlerts, state, helper,
                                                    FloorPlan, ReservationUnit, calendarService, OurMembers) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('FLOOR_PLANS.PAGE_TITLE'));

    /**
     * On Init
     */
    $ctrl.onInit = () => {
      $timeout(() => {
        $ctrl.fetch()
          .then(results => {
            const { plans, categories, units, members } = results;

            $ctrl.categories = categories.map(cat => helper.pick(cat, ['id', 'name']));

            categories.forEach(cat => { cat.reservationUnits.forEach(unit => { unit.reservationTypeId = cat.id; }); });

            $ctrl.units = categories.reduce((units, cat) => units.concat(cat.reservationUnits), [])
              .map(unit => {
                const listUnit = units.find(u => u.id === unit.id) || {};
                const result = helper.pick({ ...listUnit, ...unit }, ['id', 'name', 'description', 'area', 'capacity',
                  'thumbnail', 'photo', 'whoCanReserve', 'reservations', 'availabilities',
                  'attachedUserId', 'attachedUserName', 'availableFullTime', 'reservationTypeId']);
                const reservation = unit.reservations.find(r => r.from.isSameOrBefore(moment()) && r.to.isSameOrAfter(moment())) || {};
                if (result.attachedUserId || reservation.userId) {
                  result.$$user = members.find(m => m.id === (result.attachedUserId || reservation.userId));
                }
                return result;
              });

            // $ctrl.floorPlans = floorPlans;
            $ctrl.plan = $ctrl.plans[0];
            $ctrl.plans.forEach(plan => {
              plan.points.forEach(point => {
                point.$$unit = $ctrl.units.find(u => u.id === point.reservationUnitId);
                point.$$category = point.$$unit && $ctrl.categories.find(c => c.id === point.$$unit.reservationTypeId);
              });
              plan.points = plan.points.filter(point => !!point.$$unit);
            });
          });
      });
    };

    $ctrl.isAvailable = unit => {
      return !!unit && unit.whoCanReserve !== 'notAvailable' && unit.whoCanReserve !== 'staffOnly' && unit.availabilities.length > 0;
    };

    $ctrl.isReservable = unit => {
      return session.isAdmin() || !!unit && unit.whoCanReserve !== 'notAvailable' && unit.availabilities.length > 0;
    };

    $ctrl.reserve = () => {
      state.setParam('unit', $ctrl.point.$$unit.id);
      return state.setPath(state.getPath() + '/reservation');
    };

    $ctrl.fetchPlans = () => FloorPlan.query(state.getCurrentCenterId())
      .catch(err => dwAlerts.error(err, 'Failed to query floor plans.'))
      .then(plans => {
        $ctrl.plans = plans;
      });

    /**
     * Fetch data
     */
    $ctrl.fetch = () => {
      const promises = {};

      promises.plans = $ctrl.fetchPlans();

      promises.units = ReservationUnit.query(state.getCurrentCenterId())
        .catch(err => dwAlerts.error(err, 'Failed to query reservation units.'));

      promises.categories = calendarService.query(state.getCurrentCenterId(), moment(), moment())
        .catch(err => dwAlerts.error(err, 'Failed to query calendar.'));

      promises.members = OurMembers.query({ centerId: state.getCurrentCenterId(), limit: 10000 })
        .catch(err => dwAlerts.error(err, 'Failed to query our members.'));

      return $q.all(promises);
    };

    $ctrl.onInit();
  });
