import template from './res-bar.tpl.html';
import './res-bar.scss';

/**
 * resBar component - reservation availability bar used in Bay Reservation / Select Unit
 */
export default angular.module('deskworks.resBar', [])

  .component('resBar', {
    template,
    controller: 'ResBarCtrl',
    bindings: {
      unit: '<',
      availabilities: '=?',
    },
  })

  .controller('ResBarCtrl', function($scope, $filter) {
    const $ctrl = this;

    /**
     * $onInit
     */
    $ctrl.$onInit = function() {
      $scope.$watchCollection('$ctrl.unit.availabilities', () => {
        if (!$ctrl.unit) { return; }
        let ranges = [];
        $ctrl.unit.availabilities.forEach(period => {
          ranges.push(moment.range(period.from, period.to));
        });

        $ctrl.unit.reservations.forEach(res => {
          const resRange = moment.range(res.from, res.to);
          ranges = ranges
            .map(r => r.subtract(resRange))
            .reduce((result, ranges) => result.concat(ranges), []);
        });

        ranges = ranges.filter(r => (r.end - r.start) / 3600000 >= ($ctrl.unit.limitMin || 0));

        $ctrl.availabilities = calcAvailabilities(ranges.map(r => ({ from: r.start, to: r.end })));
      });
    };

    function calcAvailabilities(availabilities) {
      if (!availabilities || !availabilities.length) { return []; }
      const start = availabilities[0].from;
      const end = availabilities[availabilities.length - 1].to;

      return availabilities.map(av => ({ ...av, x1: dateToX(av.from), x2: dateToX(av.to) }));

      function dateToX(date) {
        return (date - start) * 100 / (end - start);
      }
    }
  });
