import template from  './floor-plan-point-settings.tpl.html';

export default angular.module('deskworks.floorPlanPointSettingsDirective', [])

  .directive('floorPlanPointSettings', function() {
    return {
      template,
      restrict: 'E',
      scope: true,
      controller: 'FloorPlanPointSettingsDirectiveCtrl as $ctrl',
      bindToController: {
        plan: '<',
        point: '=',
        categories: '<',
        units: '<',
        save: '&',
      },
    };
  })

  .controller('FloorPlanPointSettingsDirectiveCtrl', function($scope) {
    const $ctrl = this;

    /**
     * Delete point
     */
    $ctrl.deletePoint = () => {
      if (!$ctrl.point) return;
      $ctrl.plan.points.splice($ctrl.plan.points.indexOf($ctrl.point), 1);
      $ctrl.point = null;
      return $ctrl.save($ctrl.plan);
    };


    /**
     * Callbacks for angular filter
     */
    $ctrl.filterCategory = (cat) => cat.$$hasUnits || $ctrl.point && cat === $ctrl.point.$$category;
    $ctrl.filterUnit = (unit) => (unit.reservationTypeId === ($ctrl.point && $ctrl.point.$$category && $ctrl.point.$$category.id)) && (!unit.$$hasPoint || $ctrl.point.$$unit === unit);
  });
