export default angular.module('deskworks.FloorPlan', [
  'ngResource',
  'ngFileUpload',
  'deskworks.config',
])

  .factory('FloorPlan', function($resource, $timeout, Upload, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/floor-plans/:id'), {}, {
      update: { method: 'PUT' },
    });

    return {
      new: () => ({ enabled: true, pricingEnabled: true, areaEnabled: true, points: [] }),

      query: (centerId) => res.query({ centerId }).$promise,

      save: (centerId, plan) => plan.id ?
        res.update({ id: plan.id, centerId }, plan).$promise :
        res.save({ centerId }, plan).$promise
          .then(result => (plan.id = result.id, result)),

      delete: (id) => res.delete({ id }).$promise,

      /**
       * Upload image
       */
      uploadImage: (id, file) => Upload.upload({
        url: deskworksConfig.getApiAbsUrl(`/floor-plans/${id}`),
        method: 'PUT',
        withCredentials: true,
        data: { image: file },
      }),

    };
  });
