import template from  './floor-plan-embedding.tpl.html';

export default angular.module('deskworks.floorPlanEmbeddingDirective', [
  'deskworks.config',
])

  .directive('floorPlanEmbedding', function() {
    return {
      template,
      restrict: 'E',
      scope: true,
      controller: 'FloorPlanEmbeddingDirectiveCtrl as $ctrl',
      bindToController: {
        floorPlan: '<',
      },
    };
  })

  .controller('FloorPlanEmbeddingDirectiveCtrl', function($scope, state, helper, deskworksConfig) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      const origin = deskworksConfig.getApiOrigin();
      const centerId = state.getCurrentCenterId();

      $ctrl.getCode = _ => helper.join([
        '<script dw-floor-plans',
        `dw-center-id="${centerId}"`,
        $ctrl.all ? null : `dw-floor-plan-id="${$ctrl.floorPlan.id}"`,
        `dw-origin="${origin}"`,
        `src="${origin}/floor-plans.js"`,
        'class="dw-spin"></script>',
      ], ' ');
    };
  });
