import './floor-plans-community.scss';
import FloorPlan from '../services/floor-plan.service';
import floorPlansDirective from '../directives/floor-plans.directive';
import ReservationUnit from 'shared/services/inventory/reservation-unit.service';
import dwModal from 'shared/dw-modal/dw-modal.service';

export default angular.module('deskworks.FloorPlansCommunityCtrl', [
  'deskworks.reservationCategory.service',
  FloorPlan.name,
  floorPlansDirective.name,
  ReservationUnit.name,
  dwModal.name,
])

  .controller('FloorPlansCommunityCtrl', function($scope, $translate, $filter, $q, $window, $timeout,
                                                  dwModal, session, dwAlerts, state, helper,
                                                  FloorPlan, ReservationUnit, reservationCategoryService, OurMembers) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('FLOOR_PLANS.PAGE_TITLE'));

    /**
     * On Init
     */
    $ctrl.onInit = () => {
      $timeout(() => {
        $ctrl.fetch()
          .then(results => {
            const { plans, reservationCategories, reservationUnits, members } = results;

            $ctrl.categories = reservationCategories.map(cat => helper.pick(cat, ['id', 'name']));

            $ctrl.units = reservationUnits.map(unit => {
              const result = helper.pick(unit, ['id', 'name', 'description', 'area', 'capacity', 'thumbnail', 'photo',
                'attachedUserId', 'attachedUserName', 'availableFullTime',
                'fullTimeProductId', 'fullTimeProductName', 'fullTimePrice', 'fullTimeNet', 'fullTimeDiscount',
                'reservationTypeId']);
              if (result.attachedUserId) result.$$user = members.find(m => m.id === result.attachedUserId);
              return result;
            });

            $ctrl.plan = $ctrl.plans[0];
            $ctrl.plans.forEach(plan => {
              plan.points.forEach(point => {
                point.$$unit = $ctrl.units.find(u => u.id === point.reservationUnitId);
                point.$$category = point.$$unit && $ctrl.categories.find(c => c.id === point.$$unit.reservationTypeId);
              });
              plan.points = plan.points.filter(point => !!point.$$unit);
            });
          });
      });
    };

    $ctrl.isAvailable = unit => {
      return !!unit && unit.availableFullTime;
    };

    $ctrl.more = function(point) {
      dwModal.show({
        scope: $scope,
        templateUrl: 'members/our-members/our-member-modal.tpl.html',
        controller: 'OurMemberModalCtrl',
        inputs: { member: point.$$unit.$$user },
      });
    };

    $ctrl.fetchPlans = () => FloorPlan.query(state.getCurrentCenterId())
      .catch(err => dwAlerts.error(err, 'Failed to query floor plans.'))
      .then(plans => {
        $ctrl.plans = plans;
      });

    /**
     * Fetch data
     */
    $ctrl.fetch = () => {
      const promises = {};

      promises.plans = $ctrl.fetchPlans();

      promises.reservationCategories = reservationCategoryService.query(state.getCurrentCenterId())
        .catch(err => dwAlerts.error(err, 'Failed to query reservation categories.'));

      promises.reservationUnits = ReservationUnit.query(state.getCurrentCenterId())
        .catch(err => dwAlerts.error(err, 'Failed to query reservation units.'));

      promises.members = OurMembers.query({ centerId: state.getCurrentCenterId(), limit: 10000 })
        .catch(err => dwAlerts.error(err, 'Failed to query our members.'));

      return $q.all(promises);
    };

    $ctrl.onInit();
  });
